<script>
import { cartMixin } from "@/_helpers/cart-mixins.js";
export default {
  mixins: [cartMixin],
  data: () => ({
    isMobile: false,
    allItems: [],
    subTotal: 0,
    offcanvasOpen: false,
  }),
  created() {
    this.checkMobileEnv();
    this.getCartItems();
    this.allItems = this.parseItems();
    this.calculateDiscount();
  },
  computed: {
    currentUser() {
      return this.$store.getters.user;
    },
  },
  methods: {
    goToAccount() {
      console.log("Tested!");
      this.$router.push({ name: "PublicLogin" });
    },
    checkMobileEnv() {
      if (matchMedia) {
        const mq = window.matchMedia("(max-width: 768px)");
        this.isMobile = mq.matches;
        try {
          // Chrome & Firefox
          mq.addEventListener("change", () => {
            this.isMobile = mq.matches;
          });
        } catch (e1) {
          try {
            // Safari
            mq.addListener(() => {
              this.isMobile = mq.matches;
            });
          } catch (e2) {
            console.error(e2);
          }
        }
        //this.WidthChange(mq);
      }
    },
    WidthChange(mq) {
      this.isMobile = mq.matches;
    },
    removeCartItem() {
      this.$store.dispatch("cartRemove", this.choosenRow).then(() => {
        this.getTotalItems();
        this.allItems = this.parseItems();
      });
    },
    calculateDiscount() {
      let cartProducts = this.parseItems();
      let subTotal = 0;
      cartProducts.forEach((product) => {
        subTotal += Number(this.getProdutSubTotal(product));
      });
      this.subTotal = subTotal;
    },

    toggleCart() {
      document.body.classList.toggle("offcanvas-open");
      this.offcanvasOpen = !this.offcanvasOpen;
    },
  },
  mounted() {
    this.$root.$on("newCartItemsListner", (arr) => {
      this.cartData.items = arr.items;
      this.allItems = arr.productsArr;
    });
  },
  watch: {
    $route() {
      if (this.offcanvasOpen) {
        document.body.classList.toggle("offcanvas-open");
        this.offcanvasOpen = !this.offcanvasOpen;
      }
    },
  },
};
</script>

