export const cartMixin = {
  data() {
    return {
      cartData: {
        items: 0,
      },
      cartProducts: [],
      qtyError: false,
      filterKey: null,
      visitorCountry: {},
      countryName: "Rwanda",
      countryFlag: "rw.svg",
    };
  },
  computed: {
    cartItems: function () {
      return this.$store.getters.cartItems;
    },
    isUserLoggedIn: function () {
      return this.$store.getters.isLoggedIn;
    },
    currentCountryCode: function () {
      return typeof this.$store.getters.userCountry == "string"
        ? JSON.parse(this.$store.getters.userCountry).id.toLowerCase()
        : "rw";
    },
  },
  methods: {
    quantitySum() {
      let data = localStorage.getItem("cartItems");
      return data
        ? JSON.parse(data).reduce((prev, cur) => prev + Number(cur.qty), 0)
        : 0;
    },
    afteraCart() {
      return {
        items: this.cartItems.reduce((prev, cur) => prev + Number(cur.qty), 0),
        productsArr: this.parseItems(),
      };
    },
    cartTotal() {
      let data = localStorage.getItem("cartItems");
      if (data)
        return JSON.parse(data).reduce(
          (prev, cur) =>
            prev + Number(cur.qty) * Number(cur.retail_price)
        );
      else return 0;
    },
    getCartItems() {
      this.cartData.items = this.quantitySum();
    },
    getTotalItems() {
      this.cartData.items = this.cartItems.reduce(
        (prev, cur) => prev + Number(cur.qty),
        0
      );
    },
    parseItems() {
      let data = localStorage.getItem("cartItems");
      return data ? JSON.parse(data) : [];
    },
    treatAttributes(obj) {
      let result = [];
      for (let key in obj) {
        result.push(key + ":" + obj[key]);
      }
      return result;
    },
    getProdutSubTotal(product) {
      //let price = this.salePrice(product.price, product.retail_price);
      return Number(product.qty) * Number(product.retail_price);
    },
    getCartDiscount(product) {
      let subtotal = this.getProdutSubTotal(product);
      return subtotal;
    },
    updateScroll(eltID) {
      let container = document.getElementById(eltID);
      // element.scrollTop = element.scrollHeight;
      if (
        container.scrollHeight - container.clientHeight - container.scrollTop <=
        29
      ) {
        container.scrollTop = container.scrollHeight - container.clientHeight;
      }
    },
  },
};
